.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: raw;

    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.04rem;
    color: #FFF;
}

.gpt3__features-container__feature-title div {
    width: 2.375rem;
    height: 0.1875rem;
    flex-shrink: 0;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0);
    margin-bottom: 0.25rem;
}

.gpt3__features-container__feature-text {
    flex: 2;
    max-width: 45.6875rem;
    display: flex;
}

.gpt3__features-container__feature-text p {
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.875rem; /* 187.5% */
    color: var(--color-text);
}

@media screen and (max-width: 990px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .gpt3__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}